import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Ranking from '@/views/Ranking.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/ranking',
      name: 'ranking',
      component: Ranking
    }
  ]
})
